import React, { useState } from "react";
import ReactDOM from "react-dom";

const Button = ({ text, handleClick }) => {
  return <button onClick={handleClick}>{text}</button>;
};

const App = (props) => {
  const [letters, setAll] = useState([]);

  const setA = () => setAll(letters.concat("A"));
  const setB = () => setAll(letters.concat("B"));
  const setC = () => setAll(letters.concat("C"));
  const setD = () => setAll(letters.concat("D"));
  const setE = () => setAll(letters.concat("E"));
  const setF = () => setAll(letters.concat("F"));
  const setG = () => setAll(letters.concat("G"));
  const setH = () => setAll(letters.concat("H"));
  const setI = () => setAll(letters.concat("I"));
  const setJ = () => setAll(letters.concat("J"));
  const setK = () => setAll(letters.concat("K"));
  const setL = () => setAll(letters.concat("L"));
  const setM = () => setAll(letters.concat("M"));
  const setN = () => setAll(letters.concat("N"));
  const setO = () => setAll(letters.concat("O"));
  const setP = () => setAll(letters.concat("P"));
  const setQ = () => setAll(letters.concat("Q"));
  const setR = () => setAll(letters.concat("R"));
  const setS = () => setAll(letters.concat("S"));
  const setT = () => setAll(letters.concat("T"));
  const setU = () => setAll(letters.concat("U"));
  const setV = () => setAll(letters.concat("V"));
  const setW = () => setAll(letters.concat("W"));
  const setX = () => setAll(letters.concat("X"));
  const setY = () => setAll(letters.concat("Y"));
  const setZ = () => setAll(letters.concat("Z"));
  const setSpace = () => setAll(letters.concat(" "));
  const delLetter = () => {
    setAll(() => {
      const letters2 = [...letters];
      letters2.splice(letters2.length - 1, 1);
      return letters2;
    });
  };

  return (
    <div>
      <Button text="A" handleClick={setA} />
      <Button text="B" handleClick={setB} />
      <Button text="C" handleClick={setC} />
      <Button text="D" handleClick={setD} />
      <Button text="E" handleClick={setE} />
      <Button text="F" handleClick={setF} />
      <Button text="G" handleClick={setG} />
      <Button text="H" handleClick={setH} />
      <Button text="I" handleClick={setI} />
      <Button text="J" handleClick={setJ} />
      <Button text="K" handleClick={setK} />
      <Button text="L" handleClick={setL} />
      <Button text="M" handleClick={setM} />
      <Button text="N" handleClick={setN} />
      <Button text="O" handleClick={setO} />
      <Button text="P" handleClick={setP} />
      <Button text="Q" handleClick={setQ} />
      <Button text="R" handleClick={setR} />
      <Button text="S" handleClick={setS} />
      <Button text="T" handleClick={setT} />
      <Button text="U" handleClick={setU} />
      <Button text="V" handleClick={setV} />
      <Button text="W" handleClick={setW} />
      <Button text="X" handleClick={setX} />
      <Button text="Y" handleClick={setY} />
      <Button text="Z" handleClick={setZ} />
      <Button text="SPACE" handleClick={setSpace} />
      <Button text="DEL" handleClick={delLetter} />

      <p>{letters}</p>
    </div>
  );
};

const rootElement = document.getElementById("root");
ReactDOM.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>,
  rootElement
);
